body{margin:0;padding:0}*{scrollbar-width:none}.App{text-align:center;height:100vh}.App .sections{width:100%;height:calc(100vh - 70px);background:linear-gradient(90deg, #270242 0%, #7921cc 100%);position:relative;top:70px;-ms-scroll-snap-type:y mandatory;scroll-snap-type:y mandatory;scrollbar-width:none;scroll-behavior:smooth}.App .sections::-webkit-scrollbar{display:none}.App .sections>*{width:100vw;height:calc(100vh - 70px);scroll-snap-align:start;font-family:'Londrina Solid'}.work{height:100vh;background:linear-gradient(90deg, #907878 0%, #ccc0c0 100%);font-family:-apple-system,BlinkMacSystemFont,"Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif;color:#630330;font-size:1.5em}.work #github{width:25%;height:auto}.header{color:gold;font-family:-apple-system,BlinkMacSystemFont,"Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif}.navbar{display:flex;align-items:center;justify-content:center;background:#293133;width:100%;height:70px;position:fixed;top:0;z-index:2}.navbar #homeBtn{text-decoration:none;font-family:-apple-system,BlinkMacSystemFont,"Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif;color:whitesmoke;cursor:pointer;font-size:2em;transition:0.5s}.navbar #homeBtn:hover{font-size:2.5em}.navbar #mailBtn{text-decoration:none;margin-left:50px;font-family:-apple-system,BlinkMacSystemFont,"Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif;color:whitesmoke;cursor:pointer;font-size:2em;transition:0.5s}.navbar #mailBtn:hover{font-size:2.5em}.navbar .hamburger{margin-left:50px;border-style:none;fill:white;fill-opacity:90%;cursor:pointer}.navbar .hamburger:hover{fill-opacity:75%}.skills{text-align:justify;color:#630330;height:100vh;background:linear-gradient(to right, #1c92d2, #f2fcfe);font-family:-apple-system,BlinkMacSystemFont,"Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif}.skills .skillPics{display:flex;height:25%;width:auto}.skills #skillsHeader{text-align:center}.skills ul{font-size:1.25em;font-family:'Roboto', sans-serif;margin-left:20px}.skills p{font-size:1.25em;font-family:'Roboto', sans-serif;margin-left:20px}.skills #jsImg{margin-left:50px;border-radius:15%}.skills #reactImg{margin-left:50px;border-radius:15%}.profielfoto{display:block;margin-left:auto;margin-right:auto;width:50%}.profielfoto #profielfoto{width:300px;height:auto}.music{color:whitesmoke;font-size:1.5em}.music p{font-family:'Roboto', sans-serif}.music audio{margin-left:50px;background:indigo;border-radius:5%;border-style:solid;border-color:black}summary{color:gold;margin-top:100px;font-size:1.5em}#pokedex{position:absolute;display:grid;place-items:center;width:30%;height:auto}h3{font-family:'Roboto';font-weight:bold !important;font-style:italic;font-size:1.3em;margin-left:20px}h2{font-size:1.5em;margin-left:20px}.arrow{position:absolute;width:20px;height:20px;border:solid white;display:inline-block;border-width:0 3px 3px 0;transform:rotate(45deg);margin-top:230px}

